import React, { Component } from 'react';
import { Link, animateScroll as scroll } from "react-scroll";

export default class Footer extends Component {
  render() {
    return (
      <footer>
        <div className="row">
          <div className="twelve columns">
            <ul className="social-links">
              <li>
                <a href="https://github.com/snvc00" target="_blank">
                  <i className="fab fa-github"/>
                </a>
              </li>
              <li>
                <a href="https://www.linkedin.com/in/santiago-valle-463130194/" target="_blank">
                  <i className="fab fa-linkedin-in"/>
                </a>
              </li>
            </ul>
            <ul className="copyright">
              <li>Testing Documentation Tool</li>
              <li>
                Template from{" "}
                <a title="Styleshout" href="http://www.styleshout.com/">
                  Styleshout
                </a>
              </li>
              <li>
                React JS Adaptation{" "}
                <a title="GitHub" href="https://github.com/snvc00">
                  Santiago Valle
                </a>
              </li>
            </ul>
          </div>
          <div id="go-top" style={{ display: "block" }}>
            <Link to="landing" smooth={true} duration={1500}>
              <i className="icon-up-open" />
            </Link>
          </div>
        </div>
      </footer>
    )
  }
}
