import React, { Component } from 'react'
import { Link, animateScroll as scroll } from "react-scroll";

export default class Header extends Component {
  render() {
    return (
      <header id="landing">
        <div className="row banner">
          <div className="banner-text">

            <h1 className="responsive-headline">Testing</h1>
            <h1 className="responsive-headline">Documentation Tool</h1>

            <h3>
              <br></br>In this page you can find 
              <a className="text-link">
                <Link to="resume" smooth={true}>{" "}information</Link>
              </a>
              <br></br>about the tool and check the 
              <a className="text-link">
                <Link to="about" offset={20} smooth={true} duration={1600}>{" "}third-party licences</Link>.
              </a>
              
            </h3>
            <br></br>
              <a href="https://github.com/snvc00/testing-documentation-tool/archive/master.zip" className="button"style={{background: '#F06000'}}>
                <i className="fa fa-download" />
                {" "}Download Testing Documentation Tool
              </a>
            <ul className="social">
              <li>
                <a href="https://github.com/snvc00" target="_blank"><i className="fab fa-github"/></a>
              </li>
              <li>
                <a href="https://www.linkedin.com/in/santiago-valle-463130194/" target="_blank"><i className="fab fa-linkedin-in"/></a>
              </li>
            </ul>

          </div>
        </div>
        <div>
          <p className="scrolldown">
            <Link to="resume" smooth={true}><i className="icon-down-circle"/></Link>
          </p>
        </div>      
      </header>
    )
  }
}
